.cart-panel {
  display: flex;
  flex-flow: column nowrap;
  position: fixed;
  top: 0;
  right: 0;
  width: 400px;
  height: 100%;
  background: var(--color-light);
  border-left: 1px solid var(--border-color);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  z-index: 100;
  transform: translateX(100%);
  transition: transform var(--transition-duration) ease;
  z-index: 20;
}
.cart-panel:after {
  content: "";
  position: absolute;
  top: 0;
  right: 100%;
  width: calc(100vw + 400px);
  height: 100%;
  background: var(--overlay);
  opacity: 0;
  visibility: hidden;
  transition: opacity var(--transition-duration) ease;
  z-index: -1;
}
.cart-panel--open {
  transform: translateX(0);
}
.cart-panel--open:after {
  opacity: 1;
  visibility: visible;
}
.cart-panel--open .cart-panel__close {
  visibility: visible;
  opacity: 1;
}
.cart-panel__header {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  min-height: 60px;
}
.cart-panel__title {
  width: 100%;
  font-size: var(--font-size-small);
  font-weight: normal;
  padding-bottom: 1rem;
  border-bottom: 4px solid var(--color-gray-600);
}
.cart-panel__content {
  flex: 1 1 auto;
  display: flex;
  padding: 1rem;
}
.cart-panel__empty {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  text-align: center;
  min-height: 100%;
  padding: 1rem 1.5rem;
  gap: 1rem;
  width: 100%;
}
.cart-panel__empty-title {
  font-size: var(--font-size-medium);
  text-transform: uppercase;
  margin-bottom: auto;
}
.cart-panel__empty > *:last-child {
  margin-bottom: auto;
}
.cart-panel__close {
  position: absolute;
  right: calc(100% + 1px);
  top: 0;
  padding: 0.5rem;
  border: 1px solid var(--border-color);
  font-size: var(--font-size-medium);
  font-weight: 500;
  color: var(--color-dark);
  background: var(--color-light);
  cursor: pointer;
  visibility: hidden;
  opacity: 0;
  transition-duration: var(--transition-duration);
}
.cart-panel__close img,
.cart-panel__close svg {
  display: block;
}
.cart-panel__close:hover {
  background-color: var(--color-tertiary);
}